// Structure
@import './structure/fonts';
@import './structure/variables-images';
@import './structure/variables';
@import './structure/mixins';
@import './structure/animations';
@import './structure/normalize';
@import './structure/common';
@import './structure/main-menu';

// Components
@import './components/menu-toggle';
@import './components/button';
@import './components/nav-logo';
@import './components/teaser';
@import './components/cover-full';
@import './components/destaques-home';

#plnmt-capa-builder {

	display: block;
	margin: 20px 0;

	#type-chooser {
		padding: 0 20px;
		margin: 0 0 20px;
	}

	.substaques {
		padding-bottom: 38px;
	}

}

body.post-new-php,
body.post-php {

	&.post-type-capa {

		#adminmenuback,
		#adminmenuwrap {
			display: none !important;
		}
		#wpbody-content > .wrap {

			margin-right: 0;

			> #message {
				margin-left: 20px;
				margin-right: 20px;
			}
		
		}
		#wpcontent {

			margin-left: 0;
			padding-left: 0;

			.wp-heading-inline {
				padding-left: 20px;
			}
		
		}
		#post-body {

			display: flex;
			flex-direction: column;

			#post-body-content,
			#postbox-container-1 {
				width: auto;
				padding-left: 20px;
				padding-right: 20px;
			}
			#post-body-content {
				order: 1;
			}
			#postbox-container-1 {
				order: 3;
			}
			#postbox-container-2 {
				order: 2;
			}
		
			&.columns-2 #postbox-container-1 {
			    float: none;
			    margin-right: 0;
			    width: auto;
			}

		}
		#poststuff #post-body.columns-2 {
			margin-right: 0;
		}

		section.destaques-home {

			padding-bottom: 38px;
			background: #ffffff;
			transition: all .4s ease-in-out;

			&.style--2 {
				padding-top: 38px;
			}

		}

		.cover-full,
		.teaser-post {

			position: relative;

		    button {
		    	display: flex;
		    	align-items: center;
		        position: absolute;
		        left: 20px;
		        top: 20px;
		        min-height: 28px;
		        padding: 0 4px;
		        z-index: 999;
		    }

		    &.no-image button {
		    	top: -8px;
		    	right: 0;
		    	left: initial;
		    }

		}

	}

}